import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

// 获取缓存的购物车数据
var cart = JSON.parse(localStorage.getItem('cart') || '[]');

export default new Vuex.Store({
  state: {
    show: "1",
    //购物车数据结构[{id:*, count:*, price:*, selected:*}]
    cart: cart
  },
  mutations: {
    addToCart(state, goodsinfo) {
      // 默认没有对应商品
      var flag = false;

      // 若有商品，则数量叠加
      state.cart.some(item => {
        if (item.id == goodsinfo.id) {
          item.count += parseInt(goodsinfo.count);
          flag = true;
          return true;
        }
      })
      // 若无商品，则直接追加
      if (!flag) {
        state.cart.push(goodsinfo);
      }

      // 数据持久化
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    updateCart(state, goodsinfo) {
      // 更新商品数量
      // 如果有此商品，则覆盖数量，并更新缓存
      state.cart.some(item => {
        if (item.id == goodsinfo.id) {
          item.count = parseInt(goodsinfo.count);
          return true
        }
      })
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    removeCart(state, id) {
      // 删除商品
      state.cart.some((item, i) => {
        if (item.id == id) {
          state.cart.splice(i, 1)
          return true
        }
      })
      localStorage.setItem('cart', JSON.stringify(state.cart))
    },
    updateSelected(state, info) {
      state.cart.some(item => {
        if(item.id == info.id) {
          item.selected = info.selected;
        }
      })
      localStorage.setItem('cart', JSON.stringify(state.cart))
    }
  },
  getters: {
    getSelect(state) {
      // 包装一个只有id和selected状态的对象{"id1":selected1, "id2":selected2}
      var o = {};
      state.cart.forEach(item => {
        o[item.id] = item.selected;
      })
      return o;
    },
    getCountTotalPrice(state) {
      var o = {
        count: 0,
        total: 0
      }
      state.cart.forEach(item => {
        if (item.selected) {
          o.count ++;
          o.total += item.deposit * item.count;
        }
      })
      return o
    }

  }
})



