<template>
  <div id="cart">
    <div class="wrap">
      <div class="cart-content">
        <table class="cart-table">
          <tr v-for="(item,index) in cartList">
            <td width="10%" style="text-align: center">
              <!--<mt-switch v-model="value"></mt-switch>-->
              <!--记住选中状态要用v-model双向绑定-->
              <input type="checkbox" v-model="$store.getters.getSelect[item.id]" @change="selectChange(item.id, $store.getters.getSelect[item.id])">
            </td>
            <td width="30%">
              <img :src="item.image" alt="">
            </td>
            <td>
              <div class="cart-p">
                <div class="cart-title">{{item.title}}</div>
                <div class="cart-np clearfix">
                  <div class="cart-price">￥{{item.deposit}}</div>
                  <div class="cart-numberbox">
                    <number-box :count="item.count" :goodsid="item.id"></number-box>
                  </div>
                </div>
              </div>
            </td>
            <td>
              <div class="cart-option">
                <button class="cart-delete" @click="remove(item.id, index)">删除</button>
              </div>
            </td>
          </tr>
        </table>

      </div>
    </div>

    <div class="cart-bottom clearfix">
      <div class="cart-sum">结算({{$store.getters.getCountTotalPrice.count}})</div>
      <div class="cart-money">
        <span>总计：</span><em>￥{{$store.getters.getCountTotalPrice.total}}</em>
      </div>


    </div>

  </div>
</template>

<script>
  import NumberBox2 from './common/NumberBox2.vue'
  export default {
    data() {
      return {
        test: false,
        cartList: [],
        total: 0
      }
    },
    created() {
      this.getCart();
    },
    methods: {
      getCart() {
        this.cartList = JSON.parse(localStorage.getItem('cart'));
      },
      remove(id, i) {
        this.cartList.splice(i, 1)
        this.$store.commit('removeCart', id)
      },
      selectChange(id, selected) {
        this.$store.commit('updateSelected', {id: id, selected: selected})
      }

    },
    components: {
      "number-box": NumberBox2
    }

  }
</script>

<style lang="less" scoped>
  #cart {
    .cart-content{
      background-color: #fff;
    }
    .cart-table{
      width: 100%;
    }
    .cart-table tr{
      border-top: 10px solid #eee;
    }
    .cart-table td{
      padding: 10px 5px;
    }
    .cart-title{
      font-size: 15px;
      line-height: 20px;
      height: 40px;
      overflow: hidden;
    }
    .cart-np{
      position: relative;
      margin-top: 10px;
    }
    .cart-numberbox{
      position: absolute;
      top: 0;
      right: 0;
    }
    .cart-numberbox{
      transform: scale(0.6);
      transform-origin: right top;
    }
    .cart-price{
      font-size: 18px;
      color: #ff6900;
    }
    .cart-delete{
      display: inline-block;
      font-size: 12px;
      color: #ff6900;
      border: 1px solid #ff6900;
      line-height: 24px;
      padding: 0 10px;
      border-radius: 20px;
    }
    .cart-bottom{
      position: fixed;
      z-index: 99;
      left: 0;
      bottom: 60px;
      width: 100%;
      height: 40px;
      line-height: 40px;
      background-color: #fff;
      box-shadow: 0 0 5px #999;
    }
    .cart-sum{
      float: right;
      padding: 0 16px;
      text-align: center;
      background: #d61c1f;
      color: #fff;
      font-size: 16px;
    }
    .cart-money{
      float: right;
      font-size: 14px;
      margin-right: 20px;
    }
    .cart-money em{
      color: #d61c1f;
    }
  }

</style>





