<script>
  import Swiper from 'swiper'

  export default {
    mounted() {
      // 初始化swiper
      var mySwiper = new Swiper ('.index-banner .swiper-container', {
        direction: 'horizontal',
        loop: true,
        speed: 800,
        autoplay: {
          delay: 3000,
          disableOnInteraction: false,
        },
        // 如果需要分页器
        pagination: {
          el: '.index-banner .swiper-pagination',
          clickable :true,
        },
      })

      this.getProduct();
       this.getNews()
    },
    data(){
      return {
        productList: [],
        newsList: []
      }
    },
    methods: {
      getProduct() {
//        this.$http.get('/static/api/product.json').then(data => {
//          this.productList = data.body.product_body;
//        })
        this.$http.get('https://raw.githubusercontent.com/ekko-xie/ekko-xie.github.io/master/api/kuagangtong-vue/product.json').then(data => {
          this.productList = data.body.product_body;
        })
      },
      getNews() {
//        this.$http.get('/static/api/news.json').then(response => {
//          this.newsList = response.data.list;
//        })
        this.$http.get('https://ekko-xie.github.io/api/kuagangtong-vue/news.json').then(response => {
          this.newsList = response.data.list;
        })
      }
    }

  }

