<template>
  <div id="product">
    <div class="inner-banner"></div>
    <div class="bread-nav">
      <div class="wrap clearfix">
        <a href="#">首页</a>
        <span>&gt;</span>
        <a class="on" href="#">新车上市</a>
      </div>
    </div>

    <div class="wrap">
      <div class="prodcut-content clearfix">
        <div class="inc-box" v-for="item in list">
          <router-link :to="'/productdetail/' + item.id">
            <div class="inc-box-img">
              <img :src="item.image" alt="">
            </div>
            <div class="inc-box-p">
              <h4>{{item.name}}</h4>
              <p>仅需{{item.price}}元</p>
            </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
  export default {
    data() {
      return {
        "list": []
      }
    },
    mounted() {
      this.getList()
    },
    methods: {
      getList() {
//        this.$http.get('/static/api/product.json').then(data => {
//          this.list = data.body.product_body;
//        })

        this.$http.get('https://raw.githubusercontent.com/ekko-xie/ekko-xie.github.io/master/api/kuagangtong-vue/product.json').then(data => {
          this.list = data.body.product_body;
        })
      }
    }
  }
</script>


<style lang="less" scoped>
  #product {
    position: relative;
    background: #fff;

    .inner-banner{
      height: 130px;
      background: url("../assets/images/settle-banner.jpg") no-repeat center center / cover;
    }
    .bread-nav{
      padding: 14px 0;
    }
    .bread-nav a{
      float: left;
      display: inline-block;
      font-size: 12px;
      color: #848484;
    }
    .bread-nav a.on{
      color: #444444;
    }
    .bread-nav span{
      float: left;
      display: inline-block;
      font-size: 12px;
      color: #848484;
      margin: 0 2px;
    }
    .prodcut-content{
      padding-bottom: 30px;
    }
    .inc-box{
      float: left;
      margin-left: 2%;
      width: 49%;
      margin-top: 10px;
    }
    .prodcut-content .inc-box:nth-child(2n+1){
      margin-left: 0;
    }
    .inc-box-img img{
      width: 100%;
    }
    .inc-box-p{
      text-align: center;
      height: 60px;
      background-color: #eeeeee;
      padding: 0 10px;
    }
    .inc-box-p h4{
      font-size: 14px;
      line-height: 20px;
      padding-top: 10px;
      color: #6d6d6d;
      font-weight: normal;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .inc-box-p p{
      font-size: 12px;
      color: #010101;
      line-height: 20px;
      margin-top: 3px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

</style>



