<template>
  <div id="news" ref="news">
    <div class="inner-banner"></div>
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :auto-fill="false" ref="loadmore">
      <div class="wrap">
        <div class="index-news-content">
          <div class="in-box clearfix" v-for="item in list">
            <router-link to="/newsDetail">
              <div class="in-box-img">
                <img :src="item.image" alt="">
              </div>
              <div class="in-box-p">
                <h4>{{item.roomName}}</h4>
                <p>{{item.startTime | newsTime}}</p>
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </mt-loadmore>
  </div>

</template>


<script>
  export default {
    data(){
      return {
        page: 1,
        list: [],
        flag: true,
        allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了
      }
    },
    filters: {
      newsTime: function (timestr) {
        return timestr.substring(0, timestr.length - 3);
      }
    },
    mounted(){
      // 关键，处理下滑不断触发问题
      this.$nextTick(() => {
        let h = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
        this.$refs.news.style.height = h - 120 + 'px';
      });

      this.getList();
    },
    methods: {
      getList: function() {
        // 获取列表
        this.$indicator.open();
        this.page = parseInt(this.page) + 1;
        var url = 'http://data.live.126.net/livechannel/previewlist/' + this.page + '.json';
        this.$http.jsonp(url).then(data => {
          this.list = this.list.concat(data.body.live_review);
          setTimeout(() => {
            this.$indicator.close();
          }, 800)
        })
      },
      loadBottom: function() {
        // 上拉加载
        this.getList();// 上拉触发的分页查询
        this.$refs.loadmore.onBottomLoaded();// 固定方法，查询完要调用一次，用于重新定位

      }

    }
  }


</script>


<style lang="less" scoped>
  #news {
    position: relative;
    background: #fff;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;

    .inner-banner{
      height: 130px;
      background: url("../assets/images/settle-banner.jpg") no-repeat center center / cover;
    }
    .in-box{
      padding: 16px 0;
      border-bottom: 1px solid #eeeeee;
    }
    .in-box-img{
      float: left;
      width: 30%;
    }
    .in-box-img img{
      width: 100%;
    }
    .in-box-p{
      float: right;
      width: 66%;
    }
    .in-box-p h4{
      font-size: 14px;
      line-height: 18px;
      height: 36px;
      font-weight: normal;
      color: #000;
      overflow: hidden;
      /*text-overflow: ellipsis;*/
      /*white-space: nowrap;*/
    }
    .in-box-p span{
      display: inline-block;
      font-size: 12px;
      color: #939393;
      line-height: 18px;
    }
    .in-box-p p{
      font-size: 12px;
      color: #919191;
      line-height: 16px;
      height: 16px;
      margin-top: 4px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }

</style>



