<template>
  <div>
    <div class="mui-numbox" data-numbox-step='1' data-numbox-min='1' data-numbox-max='100'>
      <button class="mui-btn mui-numbox-btn-minus" type="button">-</button>
      <input class="mui-numbox-input" type="number" :value="count" @change="countChange" ref="numberbox"/>
      <button class="mui-btn mui-numbox-btn-plus" type="button">+</button>
      <!--<div>{{goodsid}}</div>-->
    </div>
  </div>
</template>

<script>
  import mui from '../../assets/lib/mui/js/mui.min'

  export default {
    mounted() {
      mui('.mui-numbox').numbox();
    },
    props: ['count', 'goodsid'],
    methods: {
      countChange() {
         console.log(this.$refs.numberbox.value)
         console.log(this.goodsid)
        this.$store.commit('updateCart', {id: this.goodsid, count: this.$refs.numberbox.value})
      }
    }
  }
</script>

<style>
  /*.pd-content .mint-tab-item-label{*/
    /*font-size: 16px;*/
  /*}*/
</style>



