<template>
  <div id="product-detail">
    <div class="wrap">
      <div class="pd-big">
        <img :src="detail.image" alt="">
      </div>
      <div class="pir-title">
        <h3>{{detail.title}}</h3>
        <p>2017款 标配 高配 另敞篷也有 颜色齐全 特惠来袭，港口批发价，参数配置、图片信息仅供参考，因汇率浮动实际价格信息请以实车并咨询客服后为准</p>
      </div>
      <div class="pir-table-content">
        <table class="pir-table">
          <tr>
            <td width="26%">指导价</td>
            <td>￥{{detail.price}}</td>
          </tr>
          <tr>
            <td>订金</td>
            <td><p class="pir-price-now">￥{{detail.deposit}}</p></td>
          </tr>
          <tr>
            <td>数量</td>
            <td>
              <!--数字输入框-->
              <number-box @getcount="getSubCount"></number-box>
            </td>
          </tr>
        </table>
      </div>

      <div class="pir-btn">
        <mt-button size="large" @click="addCart">立即抢购</mt-button>
      </div>

      <div class="pd-content">
        <mt-navbar v-model="selected">
          <mt-tab-item id="1">购买说明</mt-tab-item>
          <mt-tab-item id="2">参数配置</mt-tab-item>
        </mt-navbar>
        <!-- tab-container -->
        <mt-tab-container v-model="selected" :swipeable="true">
          <mt-tab-container-item id="1">
            <div class="inner-p">
              类型：美版
              <br>

              外观/内饰：蓝/米
              <br>

              配置：全景天窗、P01包、灯光包、外观包、停车辅助包、哈曼、二排电动、发光星标、方向盘加热、冷热杯架、轮锁、拖钩、后娱预留、魔幻雨刷
              <br>
              <br>
              <img src="../assets/images/index-banner1.jpg" alt="">
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div class="inner-p">
              <table class="pd-desc-table">
                <tr>
                  <th colspan="2">商品属性</th>
                </tr>
                <tr>
                  <td width="30%">级别</td>
                  <td width="70%">SUV</td>
                </tr>
                <tr>
                  <td>长*宽*高</td>
                  <td>5130*1933*1850</td>
                </tr>
                <tr>
                  <td>车身结构</td>
                  <td>5门7座SUV</td>
                </tr>
                <tr>
                  <td>轴距（mm）</td>
                  <td>3075</td>
                </tr>
                <tr>
                  <td>发动机</td>
                  <td>3.0T 367马力 V6</td>
                </tr>
                <tr>
                  <td>最大轴距</td>
                  <td>500N`m@1800</td>
                </tr>
              </table>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </div>

    </div>

  </div>
</template>

<script>
  import mui from '../assets/lib/mui/js/mui.min'
  import '../assets/lib/mui/css/mui.css'
  import NumberBox from './common/NumberBox.vue'

  export default {
    data() {
      return {
        id: this.$route.params.id,
        selectcount: 1,
        selected: "1",
        detail: []
      }
    },
    mounted() {
      this.getDetail();

    },
    methods: {
      getDetail() {
        // 获取商品详情
        this.$http.get('https://raw.githubusercontent.com/ekko-xie/ekko-xie.github.io/master/api/kuagangtong-vue/productdetail/' + this.id + '.json').then(data => {
          this.detail = data.body;
        })
      },
      addCart() {
        var goodsinfo = {
          id: this.id,
          title: this.detail.title,
          image: this.detail.image,
          deposit: this.detail.deposit,
          count: this.selectcount,
          price: this.detail.price,
          selected: true
        }

        // 调用vuex中的store里的方法，将商品存进vuex
        this.$store.commit("addToCart", goodsinfo)
        // console.log(this.$store.state.cart)

        this.$toast({
          message: '已加入购物车',
          position: 'middle',
          duration: 2000
        });

      },
      getSubCount(count) {
        // 获取子组件的数量
        this.selectcount = count;
        console.log(this.selectcount)
      }
    },
    components: {
      "number-box": NumberBox
    }

  }

</script>

<style lang="less" scoped>
  #product-detail{
    background-color: #fff;

    .pd-big{
      padding-top: 20px;
    }
    .pd-big img{
      width: 100%;
    }
    .pir-title{
      margin-top: 20px;
    }
    .pir-title h3{
      font-size: 18px;
      line-height: 24px;
      color: #3f3f3f;
      font-weight: normal;
    }
    .pir-title p{
      font-size: 13px;
      color: #999999;
      line-height: 20px;
      margin-top: 10px;
    }
    .pir-table-content{
      background-color: #f1f1f1;
      padding: 10px 20px;
      border-radius: 5px;
      margin-top: 10px;
      margin-bottom: 20px;
    }
    .pir-table{
      width: 100%;
      font-size: 14px;
    }
    .pir-table td{
      padding: 8px 0;
    }
    .pir-price-now{
      color: #ff4700;
      font-size: 24px;
      font-weight: bold;
    }
    .pir-btn .mint-button{
      color: #fff;
      background-color: #ff4700;
    }
    .pd-content{
      margin-top: 40px;
      border: 1px solid #ddd;
    }
    .pd-content .mint-navbar{
      background-color: #f1f1f1;
    }
    .pd-content .mint-navbar .mint-tab-item.is-selected{
      color: #ff4700;
      border-bottom: 3px solid #ff4700;
    }
    .inner-p{
      font-size: 12px;
      line-height: 20px;
      padding: 20px;
    }
    .pd-desc-table{
      width: 100%;
      border-left: 1px solid #000;
      border-top: 1px solid #000;
    }
    .pd-desc-table th{
      padding: 5px;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
    .pd-desc-table td{
      padding: 5px;
      border-right: 1px solid #000;
      border-bottom: 1px solid #000;
    }
  }


</style>




